import React, {useEffect} from 'react'
import companylogo from "../sapcard.png"



const Login=({changeIsLogin})=> {
    
 
    function init(){
        document.getElementById("pass").addEventListener("keyup", function(event){
        if(event.keyCode ===13){
            if(document.getElementById("pass").value==="Ganesh_13"){
               changeIsLogin();
            }
        }
    })
    }

    function login(){
        if(document.getElementById("pass").value==="Ganesh_13"){
            changeIsLogin();
         }
    }
    useEffect(() => {
        init()
    })

    return (
       
        <div className="w3-container w3-center">
        <img src={companylogo} /><br/>
        <br/>
        <p>PAVAN KUMAR</p><br />
    <input className="w3-margin" type="password" name="pass" id="pass" placeholder="Password" /><br />
    <button onClick={login}>Login</button>
         </div>
        
    )
}

export default Login
