import React, {useState}from 'react'
import Qadmin from './Qadmin'
import CreateAccounts from './CreateAccounts'

function Dashboard() {

    const [sapedit, setSapedit] = useState(false)

    const changeSapedit =()=>{
        setSapedit(true)
      }

     const createAccounts =()=>{
        setSapedit(false)
     } 
      const dashboard = (<div>
        <div className="w3-container">
        <div className="w3-center">
            <h1>Welcome PAVAN!</h1>
        </div>
    <div className="w3-center">
        <button onClick={createAccounts}>Create Accounts</button>
        <button onClick={changeSapedit}>SAP Questions</button>
    </div>
</div>
    </div>);

    return (<div>
        {dashboard}
    {sapedit?<Qadmin />: <CreateAccounts />}     
    </div>
        
    )
}

export default Dashboard
