import React, {useState, useEffect}from 'react'

function CreateAccounts() {

    const [users, setUsers]=useState([])
    const [user, setUser] = useState("")    
    const [pass, setPass] = useState("")
    const [userTable, setUserTable] = useState("")

    const create =()=>{
       
            var xhttp = new XMLHttpRequest();
            xhttp.onreadystatechange = function() {
              if (this.readyState === 4 && this.status === 200) {
                getUsers()
                setUser("")
                setPass("")
              }
            };
            xhttp.open("POST", "/users", true);
            xhttp.setRequestHeader("Content-type", "application/json");
            xhttp.send(JSON.stringify({user:user,pass:pass}));
          
    }
    const getUsers = () => {
        //get users to display table
        var xhttp = new XMLHttpRequest();
            xhttp.onreadystatechange = function() {
              if (this.readyState === 4 && this.status === 200) {
                  //update users
                setUsers(JSON.parse(this.responseText))
                console.log(users)
              }
            };
            xhttp.open("GET", "/users", true);
            xhttp.send();
    
          }

    

    const userRender = ()=>{
        setUserTable(users.map((user)=>{return  <li key={user._id}>{user.user} | {user.pass}<button onClick={()=>{deleteUser(user._id)}}>   <i className="fa fa-trash"></i></button></li>}))
       
     }

     const handleChange1 = (e) =>{
        setUser(e.currentTarget.value)
     }

     const handleChange2 = (e) =>{
        setPass(e.currentTarget.value)
     }

     useEffect(() => {
      getUsers()
       
     },[] )
     useEffect(() => {
      userRender()
       
     },[users] )

     const deleteUser = (id) =>{
      var xhttp = new XMLHttpRequest();
      xhttp.onreadystatechange = function() {
        if (this.readyState === 4 && this.status === 200) {
            //update users
          getUsers()
          console.log(users)
        }
      };
      xhttp.open("DELETE", "/users/"+id, true);
      xhttp.send();

    }

    return (
        <div>
             <div className="w3-container w3-center">
        <i className="fa fa-user user w3-padding"></i><br/>
        <p>CREATE ACCOUNTS</p><br />
        {//render users
        
        userTable
        
        }
    <input className="w3-margin" onChange={handleChange1} type="text" name="uname" id="uname" placeholder="user name" value={user}/><br />
    <input className="w3-margin" onChange={handleChange2} type="password" name="pass" id="pass" placeholder="Password" value={pass}/><br />
    <button onClick={create}>Create</button>
         </div>
        </div>
    )
}

export default CreateAccounts
