import React ,{useState,useEffect} from 'react'
import Login from './components/Login'

import Dashboard from "./components/Dashboard"



function App() {

const [isLogin, setIsLogin]= useState(false);

const [currentComponent, setCurrentComponent]=useState(<Login changeIsLogin={changeIsLogin} />)



   useEffect(()=>{if(isLogin){
     
   setCurrentComponent(<Dashboard />);
  } else { 
    setCurrentComponent(<Login changeIsLogin={changeIsLogin} /> )
  }}, [isLogin])

   function changeIsLogin() {
     setIsLogin(true);
   }
 


  return (
    <div className="App">
       
       {currentComponent}
        
       
     </div>
    
  );
}

export default App;
